// @codekit-prepend "scripts/lightbox.js"
// @codekit-prepend "scripts/sliders.js"
// @codekit-prepend "scripts/header.js"
// @codekit-prepend "scripts/media.js"
// @codekit-prepend "scripts/tabs.js"
// @codekit-prepend "scripts/map.js"

var scrolled = 0
var vh = $(window).height()

$(document).ready(function() {

	clickTransition()
	fadeChildren()
	menuToggle()
	lightbox()
	sliders()
	header()
	media()
	tabs()
	map()

	// Init scrolled variable
	scrolled = $(window).scrollTop()

	// Scroll home if user haven't scrolled any pixels
	setTimeout(function () {
		if (scrolled === 0 && $('body[data-section="home"]').length) {
			$('html,body').animate({scrollTop: $(window).outerHeight() }, 650, 'swing')
		}
	},15000)

	$('.portfolio_meta_JS').hover(
		function () {
			var item = $(this)
			$(this).closest('.portfolio_featured_JS').addClass('active')
		}, function () {
			$(this).closest('.portfolio_featured_JS').removeClass('active')
		}
	)

	$('.hero_btn_JS').click(function () {
		$('html,body').animate({scrollTop: $(window).height() }, 650, 'swing')
	})

	$('.map_item_JS').hover(
		function() {
			var item = $(this).data('item')
			$('[data-item="'+item+'"]').addClass('hover')
		},
		function() {
			var item = $(this).data('item')
			$('[data-item="'+item+'"]').removeClass('hover')
		}
	)

})

$(window).resize(function () { 
	
	map()
	
	$(window).width() > 1080 ? menuOpen('x') : menuClose('x') //jshint ignore:line
	vh = $(window).height()

})

$(window).scroll(function () {

	fadeChildren()
	header()

	scrolled = $(window).scrollTop()

	// Scroll home if user has scrolled less than 100vh
    clearTimeout($.data(this,'scrollTimer'))
    $.data(this,'scrollTimer', setTimeout(function() {
		if (scrolled > 0 && scrolled < vh && $('body[data-section="home"]').length) {
			$('html,body').animate({scrollTop: $(window).height() }, 650, 'swing')
		}
    }, 250))

})